footer{
    margin-top: 100px;
    background-color: #2d2e32;;
    width:100%;
    padding: 4rem 2rem 3rem 2rem;
}

.footerc{
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footerc_socials{
    display: flex;
    align-items: center;
    gap: 2rem;
    justify-content: center;
}

.footerc_socials > a > svg{
    color: white;
}

.footerc_socials > a > svg:hover{
    color:#196997;
}