.hero-container {
    margin-top: 0;
    background-color: #dacfcf36;
}

.hero-container>.hero-section {
    padding: 3.5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#waving-hand {
    margin-left: 20px;
    ;
    height: 50px;
    width: 50px;
}

.smallflex {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.smallflex>p {
    color: rgb(108, 106, 106);
    line-height: 2rem;
    font-weight: bold;
    font-size: 1.25rem;
}

.smallflex>h1 {
    line-height: 5rem;
    font-weight: bold;
}

.hero-section-links {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

#raghav-img {
    margin-top: 20px;
    border-radius: 50%;
}

.tech-stack {
    padding: 1rem 2rem 2rem 3rem;
}

.tech-stack>h3 {
    font-weight: bold;
    padding: 2rem 0 2rem 0;
}

.tech-stack>img {
    border: 2px solid red;
    margin-right: 20px;
}

@keyframes rotateAnim {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.tech-stack>img:hover {
    transform: scale(1.1);
    animation: rotateAnim 4s linear infinite
}

@keyframes morph {
    0% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }
}
.hero-img {
    margin-top: 40px;
    padding-top: 40px;
    -webkit-animation: morph 8s ease-in-out infinite;
    animation: morph 8s ease-in-out infinite;
    background-image: url("C:\Users\HP\OneDrive\Desktop\raghav-iimg.jpg");
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    border: 3px solid #2d2e32;
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    height: 35rem;
    position: relative;
    transition: all 1s ease-in-out;
    width: 35rem;
}
