.abt-container {
    margin-top: 50px;
    display: flex;
    width: 100%;
    padding: 5rem;
    justify-content: space-between;
    gap: 100px;
}

.parent {
    position: relative;
    top: 0px;
    left: 15%;
}

.image1 {
    position: relative;
    padding: -20px;
    top: 0;
    left: 0;
    height: 400px;
    width: 400px;
    border-radius: 10%;
}

@keyframes rotateAnimation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.image2 {
    background-color: white;
    border-radius: 50%;
    position: absolute;
    bottom: -30px;
    right: -50px;
    height: 150px;
    width: 150px;
    border: 1px solid #000000;
    animation: rotateAnimation 10s linear infinite;
}

.image3 {
    border-radius: 50%;
    position: absolute;
    bottom: 0px;
    right: -20px;
    height: 90px;
    width: 90px;
}

.abt-text {
    margin-left: 170px;
}

#about-me-text{
    color: blue;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.5rem;
}

.abt-text h3{
    margin-top: 25px;
    font-weight: bold;
    font-size: 1.5rem;
    line-height: 1.5;
    margin-bottom: 25px;
}

.abt-text p {
    color: #767676;
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.5;
}