.contact_content {
    width: 100%;
    margin-top: 15px;
}

.contact_title {
    display: flex;
    flex-direction: column;
}

.contact_title>p {
    margin-top: 20px;
    color: blue;
    text-transform: uppercase;
    font-size: 1.17rem;
    font-weight: bold;
}

.contact_title>h3 {
    color: rgb(25, 31, 21);
    font-weight: bold;
}

.contact_icon_box>span {
    align-items: center;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    color: #147efb;
    display: flex;
    height: 5rem;
    justify-content: center;
    width: 5rem;
}
.contact_info{
    display: flex;
    flex-direction: column;
}
.contact_info h3 {
    color: rgb(25, 31, 21);
    font-size: 1.7rem;
}
.contact_icons{
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: row;
    gap: 100px;
}

.contact_icon_box{
    display: flex;
    gap:20px;
}
.contact_info p {
    display: inline-block;
    color: rgb(81, 83, 80);
}

.contact_info a {
    display: inline-block;
    color: rgb(81, 83, 80);
    text-decoration: none;
}
.contact_info p:hover {
    color: #147efb;
}

.contact_info a:hover {
    color: #147efb;
}