.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color: #333;
    padding: 10px;
    position: fixed;
    width: 100%;
  }
  
  .brand {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .menu {
    display: none;
    flex-direction: column;
    cursor: pointer;
  }
  
  .bar {
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 5px 0;
  }
  
  .nav-items {
    display: flex;
    align-items: center;
    font-weight: bold;
  }
  
  .nav-items a {
    color: black;
    text-decoration: none;
    padding: 10px;
  }
  
  .nav-items a:hover {
    background-color: #ddd;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
    font-weight: bold;
  }
  
  .dropbtn {
    background-color: inherit;
    color: black;
    padding: 10px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    font-weight: bold;
  }

  .dropbtn:hover{
    background-color: #ddd;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  @media screen and (max-width: 600px) {
    .menu {
      display: flex;
    }
  
    .nav-items {
      flex-direction: column;
      align-items: flex-start;
      position: absolute;
      top: 60px;
      left: 0;
      width: 100%;
      background-color: white;
      z-index: 1;
      display: none;
    }
  
    .nav-items.open {
      display: flex;
    }
  
    .nav-items a,
    .dropbtn {
      width: 100%;
      text-align: left;
    }
  
    .dropdown {
      display: block;
      width: 100%;
    }
  }
  